@import "../common/mixins";
@import "../common/variable";

.header {
  &__top {
    position: relative;
    z-index: 101;
    padding: r(9) 0;
    color: $colorWhite;
    background-color: $colorOrange;
  }
  &__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    grid-gap: r(20);
  }
  &__main {
    padding: r(34) 0;
  }
  &__logo {
    position: relative;
    z-index: 101;
    width: 100%;
    max-width: r(361);
    @include max-tablet {
      max-width: r(200);
    }
    picture {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      font-size: 0;
    }
    img {
      width: 100%;
    }
  }
  &__right {}
  &__nav {
    @include max-tablet {
      position: fixed;
      inset: 0;
      z-index: 100;
      width: 100%;
      height: 100%;
      padding-inline: r(15);
      padding-top: r(200);
      background-color: $colorWhite;
      -webkit-transition: .3s;
      transition: .3s;

      pointer-events: none;
      visibility: hidden;
      opacity: 0;
      .menu-open & {
        pointer-events: visible;
        visibility: visible;
        opacity: 1;
      }
    }
    ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      list-style: none;
      grid-gap: r(40);
      @include max-tablet {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
      }
      li {
        a {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          font-weight: 600;
          color: $textColor;
          &:hover {
            text-decoration: underline;
            text-decoration-thickness: r(1);
          }
        }
        &:last-child {
          a {
            padding: r(10) r(20);
            color: $colorBrown;
            background-color: $colorYellow;
            border-radius: r(20);
            &:hover {
              color: $colorWhite;
              text-decoration: none;
              background-color: $colorOrange;
            }
          }
        }
      }
    }
  }
  .button-menu {
    z-index: 101;
  }
}